import AuthConst from '../config/AuthConst';
import http from "./http";

const getCafeterias = (idCliente) => {
  let url = AuthConst.BASE_URL + '/cafeterias/id-cliente/'+ idCliente;
  return http.get(url)
};

const getByEncargado = (idEncargado) => {
  let url = AuthConst.BASE_URL + '/cafeterias/id-encargado/'+ idEncargado;
  return http.get(url)
};

const getConSaldoAlumno = (id, idAlumno) => {
  let url = AuthConst.BASE_URL + '/cafeterias/' + id + '/saldo-alumno/' + idAlumno;
  return http.get(url)
};

const getSaldoPorCafeteria = (idCliente, idAlumno) => {
  let url = AuthConst.BASE_URL + '/cafeterias/id-cliente/' + idCliente + '/saldo-alumno/' + idAlumno;
  return http.get(url)
};

const createCafeteria = (cafeteria) => {
  let url = AuthConst.BASE_URL + '/cafeterias';
  return http.post(url, cafeteria)

};

const updateCafeteria = (cafeteria) => {
  let url = AuthConst.BASE_URL + '/cafeterias';
  return http.put(url, cafeteria)

};

const deleteCafeteria = (idCafeteria) => {
  let url = AuthConst.BASE_URL + '/cafeterias/'+ idCafeteria;
  return http.remove(url)
};
export default { getCafeterias, getByEncargado, getConSaldoAlumno, getSaldoPorCafeteria, createCafeteria, updateCafeteria, deleteCafeteria }
