<template>
<wrapper titulo="Cafeterías">
  <v-data-table :headers="headers" :items="cafeterias">

    <template v-if="$usuario.administrador" v-slot:item.acciones="{ item }">
      <v-btn icon small plain @click="abrirEditarCafeteria(item)">
        <v-icon>
          mdi-pencil
        </v-icon>
      </v-btn>

      <v-btn icon small plain  @click="abrirEliminarCafeteria(item)">
        <v-icon>
          mdi-delete
        </v-icon>
      </v-btn>
    </template>
  </v-data-table>

  <dialogo v-model="dialogOperarCafeteria" :title="cafeteriaParaOperar.id ? 'Editar cafetería' : 'Agregar cafetería'">
    <v-form v-model="formCafeteria" ref="formCafeteria" lazy-validation>
    <v-row>
      <v-col cols="12" md="6">
        <v-text-field v-model="cafeteriaParaOperar.nombre" label="Nombre" :rules="nameRules" autofocus/>
      </v-col>
      <v-col cols="12" md="6">
        <v-text-field v-model="cafeteriaParaOperar.email" label="Correo" :rules="emailRules"/>
      </v-col>
      <v-col cols="12" md="6">
        <v-text-field v-model="cafeteriaParaOperar.telefono" label="Teléfono"/>
      </v-col>
    </v-row>
    </v-form>
    <template slot="actions">
      <v-btn v-if="cafeteriaParaOperar.id" text color="primary" plain @click="editarCafeteria">Guardar</v-btn>
      <v-btn v-else text color="primary" plain @click="guardarCafeteria">Guardar</v-btn>
    </template>
  </dialogo>

  <dialogo v-model="dialogEliminarCafeteria" :title="'¿Desea eliminar ' + cafeteriaParaOperar.nombre + '?'">
    <template slot="actions">
      <v-btn text color="primary" plain @click="eliminarCafeteria">Aceptar</v-btn>
    </template>
  </dialogo>

  <v-btn v-if="$usuario.administrador" fixed color="primary" bottom right fab @click="abrirDialogCrearCafeteria">
    <v-icon>mdi-plus</v-icon>
  </v-btn>
</wrapper>
</template>

<script>
import CafeteriasService from "@/services/CafeteriasService";

export default {
  name: "Cafeterias",
  data(){
    return{
      cafeterias: [],
      dialogOperarCafeteria: false,
      headers: [
        { text: 'Nombre', value: 'nombre' },
        { text: 'Correo', value: 'email' },
        { text: 'Teléfono', value: 'telefono' },
        { text: '', value: 'acciones' }
      ],
      cafeteriaParaOperar: {},
      dialogEliminarCafeteria: false,
      formCafeteria: false,
      nameRules: [v => !!v || 'Este campo es requerido'],
      emailRules: [
        v => !!v || 'Correo es requerido',
        v => /.+@.+\..+/.test(v) || 'Ingrese un correo valido',
      ]
    }
  },
  async created() {
    this.$loader = true
    try {
      this.cafeterias = await CafeteriasService.getByEncargado(this.$usuario.id)
    } catch (e) {
      this.cafeterias = []
    }
    this.$loader = false
  },

  methods: {
    abrirEditarCafeteria(cafeteria){
      this.cafeteriaParaOperar = cafeteria
      this.dialogOperarCafeteria = true
    },
    async guardarCafeteria(){
      if (this.$refs.formCafeteria.validate()) {
        this.$loader = true
        try {
          this.cafeteriaParaOperar.activo = true
          this.cafeteriaParaOperar.idCliente = this.$usuario.idCliente
          this.cafeteriaParaOperar.cliente = this.$usuario.cliente
          let cafeteriaAgregada =  await CafeteriasService.createCafeteria(this.cafeteriaParaOperar)
          this.cafeterias.push(cafeteriaAgregada)
          this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Cafetería guardada.', true);
        }catch (e){
          this.$showAlerta('600', 'error', 'mdi-alert-circle',
            'No fue posible guardar la cafetería.', true);
        }
        this.dialogOperarCafeteria = false
        this.$loader = false
      }
    },

    abrirEliminarCafeteria(cafeteria){
      this.cafeteriaParaOperar = cafeteria
      this.dialogEliminarCafeteria = true
    },
    async editarCafeteria(){
      if (this.$refs.formCafeteria.validate()) {
        this.$loader = true
        try {
          this.cafeteriaParaOperar.activo = true
          this.cafeteriaParaOperar.idCliente = this.$usuario.idCliente
          this.cafeteriaParaOperar.cliente = this.$usuario.cliente
          let cafeteriaActualizada =  await CafeteriasService.updateCafeteria(this.cafeteriaParaOperar)
          const index = this.cafeterias.findIndex(cafeteria => cafeteria.id === cafeteriaActualizada.id)
          this.$set(this.cafeterias[index], 'cafeteria', cafeteriaActualizada)
          this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Cafetería actualizada.', true);
        }catch (e){
          this.$showAlerta('600', 'error', 'mdi-alert-circle',
            'No fue posible actualizar la cafetería.', true);
        }
        this.dialogOperarCafeteria = false
        this.$loader = false
      }

    },
    abrirDialogCrearCafeteria(){
      this.cafeteriaParaOperar = {}
      this.$nextTick(() => (this.$refs.formCafeteria.resetValidation()));
      this.dialogOperarCafeteria = true
    },
    async eliminarCafeteria(){
      this.$loader = true
      try {
        await CafeteriasService.deleteCafeteria(this.cafeteriaParaOperar.id)
        const index = this.cafeterias.findIndex(cafeteria => cafeteria.id === this.cafeteriaParaOperar.id)
        this.$delete(this.cafeterias, index)
        this.$showAlerta('600', 'success', 'mdi-check-circle-outline', 'Cafetería eliminada.', true);
      }catch (e){
        this.$showAlerta('600', 'error', 'mdi-alert-circle',
          'No fue posible eliminar la cafetería.', true);
      }
      this.dialogEliminarCafeteria = false
      this.$loader = false
    }

  }
}
</script>

<style scoped>

</style>
